<template>
  <div id="top" class="wrapper">
    <LanguageButton
      :languages="languages"
      @select-language="onSelectLanguage"
    />
    <Main :header="header"></Main>
    <Profile> </Profile>
    <Diplomas> </Diplomas>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

import LanguageButton from "./components/LanguageButton.vue";
import Main from "../components/Main";
import Profile from "../views/Profile.vue";
import Diplomas from "../views/Diplomas.vue";

export default {
  components: {
    Diplomas,
    LanguageButton,
    Main,
    Profile
  },

  bodyClass: "landing-page",
  props: {
    header: {
      type: String,
      default: require("@/assets/img/photo.png")
    },
    teamImg1: {
      type: String,
      default: require("@/assets/img/faces/avatar.jpg")
    },
    teamImg2: {
      type: String,
      default: require("@/assets/img/faces/christian.jpg")
    },
    teamImg3: {
      type: String,
      default: require("@/assets/img/faces/kendall.jpg")
    }
  },
  data() {
    return {
      name: null,
      email: null,
      languages: [
        { name: "Ru", isSelected: true },
        { name: "En", isSelected: false }
      ],
      message: null
    };
  },
  computed: {
    ...mapState(["selectedLanguageIndex"]),
    headerStyle() {
      return {
        backgroundImage: `url(${this.header})`
      };
    },
    selectedLanguage() {
      return this.languages.find(language => language.isSelected).name;
    }
  },
  methods: {
    ...mapMutations([
      // mapMutations   :
      "setSelectedLanguageIndex" // `this.setHomePageData(amount)`   `this.$store.commit('setHomePageData', amount)`
    ]),
    onSelectLanguage(selectedName) {
      this.languages.map(language =>
        language.name === selectedName
          ? (language.isSelected = true)
          : (language.isSelected = false)
      );
      this.setSelectedLanguageIndex(this.selectedLanguage === "Ru" ? 0 : 1);
    }
  }
};
</script>

